import React from "react";
import Typography from '@material-ui/core/Typography';
import Layout from '../layout/Layout';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Blog from '../components/Blog';
import { globalHistory } from "@reach/router";
import Metadata from '../components/Metadata';
import {BlogPageMetadata} from '../configs/PageMetadata';

const useStyles = makeStyles(() => ({
  msgCard: {
    margin: 20,
    padding: 20
  }
}));

export default function BlogPage() {
  const classes = useStyles();
  var blogId = undefined;
  if (globalHistory.location.hash !== undefined) {
    blogId = globalHistory.location.hash.replace("#", '');
    console.log(globalHistory.location);
  }
  else {
    blogId = false;
  }

  return (
    <Layout>
      <Metadata
        Title={BlogPageMetadata.Title}
        Description={BlogPageMetadata.Description}
      />
      { (blogId === undefined || blogId === "") ?
        (<Paper elevation={3} className={classes.msgCard} >
          <Typography variant="h5">
            Blog Not Found !
          </Typography>
        </Paper>)
        : (<Blog blogId={blogId} />)
      }
    </Layout>
  );
}